<template>
    <div id="not-logged-in" class="nav-3" v-if="!playerLoggedIn">
      <router-link to="/home">Home</router-link>
      <router-link to="/login">Login</router-link>
      <router-link to="/sign-up">Sign Up</router-link>
    </div>
    <div id="not-logged-in" class="nav-4" v-else-if="playerLoggedIn && role == 'scout'">
        <router-link to="/home">Home</router-link>
        <router-link v-if="role == 'scout'" to="/scout-home">Scout Area</router-link>
        <router-link v-if="role == 'scout'" to="/player-search">Player Search</router-link>
        <router-link to="/home"><div class="log-out-button" @click="logout">Logout</div></router-link>
    </div>
    <div id="not-logged-in" class="nav-3" v-else-if="playerLoggedIn && role == 'player'">
        <router-link to="/home">Home</router-link>
        <router-link v-if="role == 'player'" to="/player-home">Player Area</router-link>
        <router-link to="/home"><div class="log-out-button" @click="logout">Logout</div></router-link>
    </div>
</template>

<script>
import firebase from 'firebase'
import {database} from '../main'

export default {
    name: 'Nav',
    created() {
        this.checkRole()
    },
    data() {
        return {
            notLoggedIn: false,
            playerLoggedIn: false,
            role:''
        }

    },

    methods: {
        checkRole()
        {
            let self = this
            firebase.auth().onAuthStateChanged(function(user){
                if(user) {
                    var userId = firebase.auth().currentUser.uid 
                    console.log(userId)
                    let loggedIn = true
                    self.playerLoggedIn = loggedIn;
                    console.log('logged in')

                    firebase.database().ref().child('Players').orderByKey().equalTo(userId).once('value', function(snapshot){
                        console.log(snapshot.val())

                        if(snapshot.val()){
                            self.role = 'player'
                            console.log('player')
                        }
                        else{
                            self.role = 'scout'
                            console.log('scout')
                        }
                    })

                } else {
                    let loggedIn = false
                    self.playerLoggedIn = loggedIn;
                }

            })
        },
        logout: function(){
            firebase.auth().signOut().then(() => {
                this.$router.replace('home')
            })
        }
        
    }
}

</script>

<style scoped>
.nav {
  padding: 30px;
}

.nav a {
  font-weight: bold;
  color: #2c3e50;
}

.nav a.router-link-exact-active {
  color: #42b983;
}
</style>

